.disabled-child {
  padding: 6.25em 0 3.125em;
  &__content {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  &__left {
    width: calc(100% - 29.9375em);
  }
  &__right {
    width: 25.5625em;
    margin-left: 4.375em;
  }
  &__img {
    background:  #f2f2f2;
    border-radius: 0.3125em;
    padding: 0.875em;
    img {
      width: 100%;
    }
  }
  &__items {
    display: grid;
    grid-template-columns: 100%;
    gap: 1.5625em;
  }
  &__item {
    display: flex;
    align-items: center;
    background: #ffffff;
    box-shadow: 0.1875em 0.0625em 1.5625em 0.125em rgba(0,0,0,0.05);
    border-radius: 0.375em;
    padding: 0.75em 1.875em;
    position: relative;
    &:nth-child(odd) {
      border-bottom: 0.3125em solid #8bb314;;
    }
    &:nth-child(even) {
      border-bottom: 0.3125em solid #f7b001;;
    }
    &-img {
      flex-shrink: 0;
      width: 5.375em;
      position: relative;
      img {
        width: 100%;
      }
      span {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        font-size: 2.125em;
        line-height: 120%;
        font-weight: 700;
        text-shadow: 0 0.1875em 0.1875em rgba(139,179,20,0.41);
        color: #ffffff;
      }
    }
    &-desc {
      margin-left: 1.25em;
    }
    &-title {
      span {
        font-size: 1.0625em;
        line-height: 1.25em;
        font-weight: 600;
        color: #2c2c2c;
      }
    }
    &-text {
      margin-top: 0.625em;
      p {
        font-size: 0.875em;
        line-height: 140%;
        color: #666666;
      }
    }
    &-icon {
      position: absolute;
      top: 50%;
      left: calc(100% - 1.25em);
      transform: translate(0, -50%);
      width: 1.6875em;
      img {
        width: 100%;
      }
    }
  }
}

.forms-paralysis {
  padding: 3.125em 0;
  &__grid {
    display: grid;
    grid-template-columns: repeat(3, calc(100% / 3 - 4.625em / 3));
    gap: 2em 2.3125em;
    grid-auto-rows: 1fr;
  }
}
.form-paralysis__card {
  background-image: linear-gradient(-30.0deg, rgba(0,0,0,0.01) 42%, rgba(21,68,95,0.0) 57%);
  border: 0.0625em solid #f4f3f3;
  border-radius: 1.875em 0;
  padding: 1.4375em 2em;
  position: relative;
  &-title {
    span {
      font-size: 1.0625em;
      line-height: 120%;
      font-weight: 600;
      color: #2c2c2c;
    }
  }
  &-text {
    margin-top: 0.875em;
    p {
      font-size: 0.875em;
      line-height: 140%;
      color: #666666;
    }
  }
  &-number {
    position: absolute;
    top: -0.625em;
    right: -0.625em;
    width: 3.5625em;
    img {
      width: 100%;
    }
    span {
      position: absolute;
      top: 0.325em;
      right: 0.325em;
      font-size: 1.5em;
      line-height: 100%;
      font-weight: 700;
      color: #ffffff;
    }
  }
}

.symptoms-gout {
  padding: 3.125em 0;
  overflow: hidden;
  &__flex {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    width: calc(100% + 2em);
  }
  ._green {
    .symptom-gout__card-bg {
      background: #8bb314;
    }
    .symptom-gout__card-number {
      background: #8bb314;
      border-radius: 0 1.8125em 1.8125em 0;
    }
  }
  ._orange {
    .symptom-gout__card-bg {
      background: #f7b001;
    }
    .symptom-gout__card-number {
      background: #f7b001;
      border-radius: 0 1.8125em 1.8125em 0;
    }
  }
  .width--33 {
    width: calc(100% /3 - 5.9375em);
  }
}
.symptom-gout__card {
  width: calc(50% - 5.9375em);
  background: #ffffff;
  border: 0.0625em solid #f4f3f3;
  box-shadow: 0 0 1.6875em 0 rgba(133,144,137,0.34);
  border-radius: 1.875em 1.875em 1.875em 0;
  padding: 1.5625em 1.5625em 1.5625em 5.6875em;
  margin: 1.875em 2em 2em 3.9375em;
  position: relative;
  .symptom-gout__card-bg {
    position: absolute;
    z-index: -1;
    top: -1.875em;
    left: -3.9375em;
    width: calc(100% - 4.5625em);
    height: 8.125em;
    border-radius: 1.875em 1.875em 1.875em 0;
  }
  &-title {
    font-size: 1.0625em;
    line-height: 1.25em;
    font-weight: 600;
    color: #2c2c2c;
  }
  &-text {
    margin-top: 0.875em;
    p {
      font-size: 0.875em;
      line-height: 140%;
      color: #666666
    }
  }
  &-number {
    position: absolute;
    z-index: 3;
    top: 2.625em;
    left: -3.9375em;
    width: 8.125em;
    padding: 0.9375em;
    display: flex;
    justify-content: space-between;
    align-items: center;
    i {
      display: flex;
      justify-content: center;
      align-items: center;
      width: 2.25em;
      img {
        width: 100%;
      }
    }
    span {
      font-size: 2.0625em;
      line-height: 80%;
      font-weight: 700;
      color: #ffffff;
    }
  }
}

.care {
  padding: 6.25em 0;
  &__content {
    display: flex;
    justify-content: space-between;
    position: relative;
    ._line {
      position: absolute;
      z-index: 1;
      top: -3.125em;
      left: 50%;
      transform: translate(-50%, 0);
      width: 0.125em;
      height: calc(100% + 6.25em);
      background: #2c2c2c;
    }
    &:after {
      content: '';
      position: absolute;
      z-index: 1;
      top: -3.125em;
      left: 50%;
      transform: translate(-50%, 0);
      width: 0.625em;
      height: 0.625em;
      background: #2c2c2c;
      border-radius: 50%;
    }
    &:before {
      content: '';
      position: absolute;
      z-index: 1;
      left: 50%;
      bottom: -3.125em;
      transform: translate(-50%, 0);
      width: 0.625em;
      height: 0.625em;
      background: #2c2c2c;
      border-radius: 50%;
    }
  }
  &__left {
    width: 50%;
    .care__item {
      padding: 0 2.6875em 0 2.0625em;
      &-title {
        &:before {
          left: 0;
        }
      }
      &-number {
        position: absolute;
        z-index: 1;
        top: 0;
        right: -2em;
        ._border {
          position: absolute;
          top: 50%;
          right: -1.8125em;
          transform: translate(-50%, -50%);
          width: 2.5625em;
          height: 5.125em;
          border: 0.125em solid #2c2c2c;
          border-radius: 0 100% 100% 0 / 0 50% 50% 0;
        }
      }
    }
  }
  &__right {
    width: 50%;
    margin-top: 8.125em;
    .care__item {
      padding: 0 2.0625em 0 2.6875em;
      &-title {
        &:before {
          right: 0;
        }
      }
      &-number {
        position: absolute;
        z-index: 1;
        top: 0;
        left: -2em;
        ._border {
          position: absolute;
          top: 50%;
          left: 0.75em;
          transform: translate(-50%, -50%);
          width: 2.5625em;
          height: 5.125em;
          border: 0.125em solid #2c2c2c;
          border-radius: 100% 0 0 100% / 50% 0 0 50%;
        }
      }
    }
  }
  &__item {
    position: relative;
    &:not(:first-child) {
      margin-top: 6.25em;
    }
    &-top {
      display: flex;
      justify-content: space-between;
    }
    &-title {
      width: 100%;
      position: relative;
      padding: 0 0.875em;
      padding-bottom: 0.625em;
      &:after {
        content: '';
        position: absolute;
        left: 0;
        bottom: 0;
        width: 100%;
        height: 0.125em;
        background: #2c2c2c;
      }
      &:before {
        content: '';
        position: absolute;
        z-index: 1;
        bottom: -0.25em;
        width: 0.625em;
        height: 0.625em;
        background: #2c2c2c;
        border-radius: 50%;
      }
      span {
        font-size: 1.125em;
        line-height: 120%;
        font-weight: 700;
        color: #2c2c2c;
      }
    }
    &-number {
      &-text {
        display: flex;
        justify-content: center;
        align-items: center;
        width: 4em;
        height: 4em;
        background: #8bb314;
        border-radius: 50%;
        position: relative;
        z-index: 2;
        span {
          font-size: 2em;
          line-height: 120%;
          font-weight: 700;
          color: #ffffff;
        }
      }
      ._border {
        pointer-events: none;
      }
    }
    &-text {
      margin-top: 0.875em;
      padding: 0 0.875em;
      p {
        font-size: 0.9375em;
        line-height: 140%;
        color: #666666;
      }
    }
  }
}

.advantages-center {
  padding: 3.125em 0;
  &__content {
    min-height: 37.5em;
    position: relative;
  }
  ._green {
    color: #ffffff;
    &:after {
      background: #8bb314;
    }
  }
  ._orange {
    color: #ffffff;
    &:after {
      background: #f7b001;
    }
  }
  ._gray {
    color: #666666;
    &:after {
      background: #ecedee;
    }
  }
  .hexagon {
    position: absolute;
    top: 6.6875em;
    left: 6.9375em;
    width: 8.6875em;
    height: 7.5em;
    background: #ecedee;
    clip-path: polygon(25% 0%, 75% 0%, 100% 50%, 75% 100%, 25% 100%, 0% 50%);
  }
  .hexagon2 {
    position: absolute;
    left: 8.875em;
    bottom: 2.3125em;
    width: 5.375em;
    height: 4.6875em;
    background: #ecedee;
    clip-path: polygon(25% 0%, 75% 0%, 100% 50%, 75% 100%, 25% 100%, 0% 50%);
  }
  .hexagon3 {
    position: absolute;
    top: 7.25em;
    right: 6.8125em;
    width: 8.5em;
    height: 7.375em;
    background: #ecedee;
    clip-path: polygon(25% 0%, 75% 0%, 100% 50%, 75% 100%, 25% 100%, 0% 50%);
  }
  .hexagon4 {
    position: absolute;
    top: 5em;
    right: 2.625em;
    width: 5.375em;
    height: 4.6875em;
    background: #ecedee;
    clip-path: polygon(25% 0%, 75% 0%, 100% 50%, 75% 100%, 25% 100%, 0% 50%);
  }
}
.advantage-center__card {
  width: 16.75em;
  height: 14.5em;
  position: relative;
  &:nth-child(1) {
    position: absolute;
    top: 15.3125em;
    left: 0.75em;
  }
  &:nth-child(2) {
    position: absolute;
    top: 7.6875em;
    left: 14em;
  }
  &:nth-child(3) {
    position: absolute;
    left: 14em;
    bottom: 0;
  }
  &:nth-child(4) {
    position: absolute;
    top: 15.5em;
    left: 50%;
    transform: translate(-50%, 0);
  }
  &:nth-child(5) {
    position: absolute;
    top: 0;
    left: 50%;
    transform: translate(-50%, 0);
  }
  &:nth-child(6) {
    position: absolute;
    top: 7.6875em;
    right: 14em;
  }
  &:nth-child(7) {
    position: absolute;
    right: 14em;
    bottom: 0;
  }
  &:nth-child(8) {
    position: absolute;
    top: 15.3125em;
    right: 0.75em;
  }
  &:after {
    content: '';
    position: absolute;
    z-index: -1;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    //clip-path: polygon(50% 0%, 100% 25%, 100% 75%, 50% 100%, 0% 75%, 0% 25%);
    clip-path: polygon(25% 0%, 75% 0%, 100% 50%, 75% 100%, 25% 100%, 0% 50%);  }
  &-wrap {
   position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    padding: 0.9375em 3.4375em;
  }
  &-icon {
    max-width: 3.375em;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-shrink: 0;
    margin: 0 auto;
    img {
      width: 100%;
    }
  }
  &-desc {
    margin-top: 0.3125em;
  }
  &-number {
    span {
      font-size: 2.25em;
      line-height: 120%;
      font-weight: 700;
    }
  }
  &-text {
    margin-top: 0.3125em;
    p {
      font-size: 0.9375em;
      line-height: 130%;
      font-weight: 600;
      @media screen and (min-width: 993px) {
        overflow: hidden;
        display: -webkit-box;
        -webkit-line-clamp:4;
        -webkit-box-orient: vertical;
      }
    }
  }
}


@media screen and (max-width: 992px) {
  .symptoms-gout {
    .width--33 {
      width: calc(50% - 5.9375em);
    }
  }
  .symptom-gout__card {
    &:nth-child(odd) {
      .symptom-gout__card-bg {
        background: #8bb314;
      }
      .symptom-gout__card-number {
        background: #8bb314;
      }
    }
    &:nth-child(even) {
      .symptom-gout__card-bg {
        background: #f7b001;
      }
      .symptom-gout__card-number {
        background: #f7b001;
      }
    }
  }
  .advantages-center {
    &__content {
      min-height: 100%;
      display: grid;
      grid-template-columns: repeat(2, calc(50% - 1em / 2));
      gap: 1em;
    }
    ._green {
      background: #8bb314;
    }
    ._orange {
      background: #f7b001;
    }
    ._gray {
      background: #ecedee;
    }
    .hexagons {
      display: none;
    }
  }
  .advantage-center__card {
    position: static !important;
    width: auto;
    height: auto;
    transform: unset !important;
    padding: 2em;
    &:after {
      display: none;
    }
    &-wrap {
      position: static;
      padding: 0;
    }
  }
}

@media screen and (max-width: 768px) {
  .disabled-child {
    &__content {
      flex-direction: column;
    }
    &__left {
      width: 100%;
      margin: 0 0 2em 0;
    }
    &__right {
      order: -1;
      width: 100%;
      max-width: 25em;
      margin: 0 auto;
    }
    &__item {
      padding: 0.75em 1.875em 0.75em 1.25em;
    }
  }
  .forms-paralysis {
    &__grid {
      grid-template-columns: repeat(2, calc(50% - 1.25em / 2));
      gap: 1.25em;
    }
  }
  .symptoms-gout {
    &__flex {
      display: block;
      width: 100%;
    }
    .width--33 {
      width: calc(100% - 3.9375em);
    }
  }
  .symptom-gout__card {
    width: calc(100% - 3.9375em);
    margin: 1.875em 0 0 3.9375em;
    &:not(:last-child) {
      margin-bottom: 3.875em;
    }
  }
}

@media screen and (max-width: 576px) {
  .disabled-child {
    &__item {
      display: block;
      &-desc {
        margin: 1em 0 0 0;
      }
    }
  }
  .forms-paralysis {
    &__grid {
      grid-template-columns: 100%;
    }
  }
  .care {
    padding: 3.125em 0;
    &__content {
      display: block;
      &:after {
        display: none;
      }
      &:before {
        display: none;
      }
      ._line {
        display: none;
      }
    }
    &__left {
      width: 100%;
    }
    &__right {
      width: 100%;
      margin-top: 3.125em;
      .care__item {
        &-title {
          &:before {
            left: 0;
          }
        }
      }
    }
    &__item {
      padding: 0 !important;
      &:not(:first-child) {
        margin-top: 3.125em;
      }
      &-title {
        padding-right: 0;
      }
      &-text {
        padding-right: 0;
      }
      &-number {
        display: none;
      }
    }
  }
  .advantage-center__card {
    padding: 1.25em;
  }
}

@media screen and (max-width: 450px) {
  .symptoms-gout {
    .width--33 {
      width: 100%;
    }
  }
  .symptom-gout__card {
    width: 100%;
    margin: 0;
    padding: 4em 1.25em 1.25em;
    &:not(:last-child) {
      margin-bottom: 2em;
    }
    &-bg {
      display: none;
    }
    &-number {
      top: 0;
      left: 0;
      padding-left: 1.25em;
    }
  }
  .advantages-center {
    &__content {
      grid-template-columns: 100%;
    }
  }
}